import React from 'react';

const style = {
    minHeight: "5vh",
}

const NavDots = ({ratingQuestion}) => {
    return (
        <div style={style}>
            <svg stroke="rgb(48,109,120)"  strokeWidth="1" width="75%" viewBox="0 0 500 50">
            {
                [0,1,2,3,4,5,6].map(c => {
                    const distance = 500 / 8
                    return <circle cx={(c+1) * distance} fill={ratingQuestion === c ?  "rgb(48,109,120)" : "transparent"} key={c} cy="20" r="9"/>
                })
            }
            </svg>
        </div>
    )
}

export default NavDots;


