
import React from 'react';
import FadeInAnimation from '../stylesheets/fadeinanimation.css'

const style = {
    marginTop: 0,
    width: "65%",
    display: "block",
    marginLeft: "auto",
    marginRight: "auto",
    marginBottom: 0,
    fontSize:"155%",
    paddingBottom: "5%",
    paddingTop: "10%",
    minHeight: "10vh",
}

const RatingQuestion = ({ratingQuestion}) => {
    return (
        <div>
            <p style={{...style, ...FadeInAnimation}} className='fadeIn' key={ratingQuestion}>
                {
                    ratingQuestion === 0 ? <b>How challenging was your day?</b> : 
                    ratingQuestion === 1 ? <b>How successful was your day?</b> : 
                    ratingQuestion === 2 ? <b>How much do you love what you did?</b> : 
                    ratingQuestion === 3 ? <b>How much did you do you are good at?</b> : 
                    ratingQuestion === 4 ? <b>How much did you do for yourself?</b> : 
                    ratingQuestion === 5 ? <b>How much did you do for others?</b> : 
                                            <b>How happy are you? <br/></b> 
                }
            </p>
        </div>
    )
}

export default RatingQuestion;



