import React from 'react';
import logo from '../design/naikan-logo.png'
import FadeInAnimation from '../stylesheets/fadeinanimation.css'

// import Background from '../design/main.PNG'

const style = {
    // backgroundImage: `url(${Background})`,
    background: "linear-gradient(rgb(236, 214, 86), rgb(243, 189, 76))",
    backgroundColor: "rgb(243, 189, 76)",
    minHeight: "100vh",
    textAlign: 'center',
    color: "rgb(14, 89, 114)"
}

const MainPage = (props) => (
    <div style = {{...style, ...FadeInAnimation}} onClick={props.goNext}>
        <img src={logo} alt="Naikan logo" style={{width: "50%", display: "block", marginLeft: "auto", marginRight: "auto", paddingTop: "33%"}} className="fadeIn delay-short"/>
        <h1 style={{marginTop: 10, marginBottom: 0, fontSize:"300%"}} className="fadeIn delay-short">
            NAIKAN
        </h1>
        <h1 style={{marginTop: 10, marginBottom: 0, fontSize:"150%"}} className="fadeIn delay-short">
            <i>PROTOTYPE</i>
        </h1>
        <p style={{width: "68%", display: "block", marginLeft: "auto", marginRight: "auto", marginTop: 30, marginBottom: 0, fontSize:"150%"}} className="fadeIn delay-middle">
            Stress is a state of mind and so is happiness. Only you can make the  difference.
        </p>
        <p style={{marginTop:"10vh", marginBottom: 0}} className="fadeIn delay-long">
            click anywhere to start
        </p>
    </div>
)

export default MainPage;



