import React from 'react';
import SpiderDiagram from '../components/spiderdiagram'
import CurrentDay from '../components/currentday'

const style = {
  // backgroundImage: `url(${Background})`,
  backgroundRepeat: 'no-repeat',
  backgroundSize: "100% 100%",
  background: "linear-gradient(rgb(249, 238, 183), rgb(255, 213, 123))",
  // backgroundColor: "rgb(243, 189, 76)",
  minHeight: "100vh",
  textAlign: 'center',
  color: "rgb(14, 89, 114)",
}


const StatPage = ({data}) =>{
    return (
      <div style={style}>
        <CurrentDay/>
        <SpiderDiagram data= {data}/>
      </div>
    );
}

export default StatPage;


