import React from 'react';

const style = {
    minHeight: "18",
}

const RatingCircle = ({onClick, rating}) => {
    return(
        <div style = {style}>
            <svg fill="rgb(252,226,154)" stroke="rgb(48,109,120)" width= "75%" height= "75%" viewBox="0 0 200 200">
                {/* <defs>
                    <radialGradient id="exampleGradient">
                    <stop offset="66%" stop-color="rgb(12,204,185)"/>
                    <stop offset="100%" stop-color="transparent"/>
                    </radialGradient>
                </defs> */}
                {/* <rect width="220" height= "220" stroke-width="0"/> */}
                <circle cx="100" cy="100" r="99" strokeWidth="1" fill="transparent" />
                <circle cx="100" cy="100" r="75" strokeWidth="1" fill="transparent" />
                <circle cx="100" cy="100" r="50" strokeWidth="1" fill="transparent" />
                <circle cx="100" cy="100" r="25" strokeWidth="1" fill="transparent" />
            
                <circle onClick={onClick} cx="100" cy="100" r={rating * 25 + 25 - 1} strokeWidth="0" fill="rgb(12,204,185)" />
            </svg>
        </div>
    )
}

export default RatingCircle;


