import React from 'react';

const style = {
    minHeight: "20vh",
}


const SpiderDiagram = ({data}) => {
    return(
        <div style = {style} className="fadeIn delay-middle">
            <svg fill="rgb(48,109,120)" stroke="rgb(48,109,120)" width= "85%" height= "85%" viewBox="0 0 250 280">
                <text x={midPoint.x} y="20" textAnchor='middle'>challenge</text>
                <text x={midPoint.x} y="20" textAnchor='middle' transform={"rotate(-60, " + midPoint.x + " , " + midPoint.y + ")"}>for love</text>
                <text x={midPoint.x} y="20" textAnchor='middle' transform={"rotate(60, " + midPoint.x + " , " + midPoint.y + ")"}>success</text>
                <text x={midPoint.x} y="20" textAnchor='middle' transform={"rotate(-120, " + midPoint.x + " , " + midPoint.y + ")"}>for yourself</text>
                <text x={midPoint.x} y="20" textAnchor='middle' transform={"rotate(120, " + midPoint.x + " , " + midPoint.y + ")"}>love</text>
                <text x={midPoint.x} y="250" textAnchor='middle'>good at</text>

                <circle cx={midPoint.x} cy={midPoint.y} r="99" strokeWidth="1" fill="transparent" />
                <circle cx={midPoint.x} cy={midPoint.y} r="75" strokeWidth="1" fill="transparent" />
                <circle cx={midPoint.x} cy={midPoint.y} r="50" strokeWidth="1" fill="transparent" />
                <circle cx={midPoint.x} cy={midPoint.y} r="25" strokeWidth="1" fill="transparent" />

                <polygon fill='red' fill-opacity="0.4" stroke='transparent' points =
                {
                    data.map((rating, idx) => {
                        return polygonPoints[idx][rating][0] + ',' + polygonPoints[idx][rating][1] + ' '
                    })
                }/>
                {/* {
                    polygonPoints.map(cat => 
                        cat.map((p)=>{
                            console.log(p)
                            return <circle cx = {p[0]} cy = {p[1]} r="5"/>
                        })
                    )
                } */}
                {/* <circle onClick={onClick} cx="100" cy="100" r={rating * 25 + 25 - 1} strokeWidth="0" fill="rgb(12,204,185)" /> */}
            </svg>
        </div>
    )
}

export default SpiderDiagram;




const radians = degree => degree * (Math.PI / 180);
const midPoint = {x: 125, y: 130};
const verticalPoints = [[midPoint.x, midPoint.y - 25], [midPoint.x, midPoint.y - 50], [midPoint.x, midPoint.y - 75], [midPoint.x, midPoint.y - 99]]
const polygonPoints = [
    verticalPoints,
    verticalPoints.map(p=>{
        const newX = (p[0] - midPoint.x) * Math.cos(radians(60)) - (p[1] - midPoint.y) * Math.sin(radians(60)) + midPoint.x
        const newY =  (p[0] - midPoint.x) * Math.sin(radians(60)) + (p[1] - midPoint.y) * Math.cos(radians(60)) + midPoint.y
        return [newX, newY]
    }),
    verticalPoints.map(p=>{
        const newX = (p[0] - midPoint.x) * Math.cos(radians(120)) - (p[1] - midPoint.y) * Math.sin(radians(120)) + midPoint.x
        const newY =  (p[0] - midPoint.x) * Math.sin(radians(120)) + (p[1] - midPoint.y) * Math.cos(radians(120)) + midPoint.y
        return [newX, newY]
    }),
    verticalPoints.map(p=>{
        const newX = (p[0] - midPoint.x) * Math.cos(radians(180)) - (p[1] - midPoint.y) * Math.sin(radians(180)) + midPoint.x
        const newY =  (p[0] - midPoint.x) * Math.sin(radians(180)) + (p[1] - midPoint.y) * Math.cos(radians(180)) + midPoint.y
        return [newX, newY]
    }),
    verticalPoints.map(p=>{
        const newX = (p[0] - midPoint.x) * Math.cos(radians(240)) - (p[1] - midPoint.y) * Math.sin(radians(240)) + midPoint.x
        const newY =  (p[0] - midPoint.x) * Math.sin(radians(240)) + (p[1] - midPoint.y) * Math.cos(radians(240)) + midPoint.y
        return [newX, newY]
    }),
    verticalPoints.map(p=>{
        const newX = (p[0] - midPoint.x) * Math.cos(radians(300)) - (p[1] - midPoint.y) * Math.sin(radians(300)) + midPoint.x
        const newY =  (p[0] - midPoint.x) * Math.sin(radians(300)) + (p[1] - midPoint.y) * Math.cos(radians(300)) + midPoint.y
        return [newX, newY]
    })
]



