import React from 'react';
import NextButton from '../components/nextbutton'
import RatingCircle from '../components/ratingcircle'
import NavDots from '../components/navdots'
// eslint-disable-next-line
import Background from '../design/rating1.PNG'
import RatingQuestion from '../components/ratingquestion';
import { Grid } from '../../node_modules/@material-ui/core';



class RatingPage extends React.Component {
    constructor(){
        super()
        this.state = {
            rating: 0,
            inc: 1
        }
        this.handleClick = this.handleClick.bind(this);
        this.handleNext = this.handleNext.bind(this);
    }
    
    handleClick(){
        this.setState(({rating, inc})=>{
            if(rating===3){
                return {
                    rating: 2,
                    inc: -1
                }
            }
            if(rating===0){
                return {
                    rating: 1,
                    inc: 1
                }
            }
            return {
                rating: rating + inc
            }
        })
    }

    handleNext(){
        this.setState({
            rating: 0,
            inc: 1
        })
        this.props.goNext(this.state.rating)
    }

    render(){
        const {ratingQuestion} = this.props
        return(
            <div>
                <Grid container direction="column" justify="space-between" style={{minHeight:"99vh"}}>
                    <Grid item>
                        <RatingQuestion ratingQuestion={ratingQuestion}/>
                        <RatingCircle onClick={this.handleClick} rating={this.state.rating}/>
                        <NextButton onClick={this.handleNext}/>
                    </Grid>
                    <Grid item>
                        <NavDots ratingQuestion = {ratingQuestion}/>
                    </Grid>
                </Grid>
            </div>
        )
    }
}

export default RatingPage;



