
import React from 'react';
import FadeInAnimation from '../stylesheets/fadeinanimation.css'
import moment from 'moment'

const style = {
    marginTop: 0,
    paddingTop: "15%",
    paddingBottom: "5%",
    width: "65%",
    display: "block",
    marginLeft: "auto",
    marginRight: "auto",
    marginBottom: 0,
    fontSize:"250%",
    minHeight: "15vh",
}

const CurrentDay = ({ratingQuestion}) => {
    return (
        <div>
            <p style={{...style, ...FadeInAnimation}} className='fadeIn' key={ratingQuestion}>
                {
                    moment().format('dddd')
                }
                <br/>
                {
                     moment().format("MMMM Do YYYY")
                }
            </p>
            <br/>
        </div>
    )
}

export default CurrentDay;



