import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid'
import Hidden from '@material-ui/core/Hidden';

import MainPage from './pages/main'
import RatingPage from './pages/rating'
import StatPage from './pages/statistics'
import "./index.css"

const style = {
  background: "linear-gradient(rgb(249, 238, 183), rgb(255, 213, 123))",
  minHeight: "100vh",
  textAlign: 'center',
  color: "rgb(14, 89, 114)",
}

class App extends Component {
  constructor(){
    super()
    this.state = {
      currentPage: -1,
      ratingData: [0,0,0,0,0,0]
      // currentRatingPage: 0,      
      // challengingRating: 0,
    }
    this.handleNextRating = this.handleNextRating.bind(this);
    this.handleMainClick = this.handleMainClick.bind(this);
  }
  handleMainClick(){
    this.setState({currentPage:0})
  }

  handleNextRating(rating){
    this.setState(({currentPage, ratingData})=>{
      const newRatingData = ratingData
      if(currentPage<=5){
        newRatingData[currentPage] = rating
      }
      return {
        ratingData: newRatingData,
        currentPage: currentPage + 1
      }
    })
  }

  render() {
    const {currentPage} = this.state 
    return (
      <Grid container style={{backgroundColor:"rgb(249, 238, 183)"}}>
        <Hidden only='xs'>
          <Grid sm={4}>
          </Grid>
        </Hidden>
        <Grid item xs={12} sm={4} style={style}>
          { currentPage === -1 ? <MainPage goNext={this.handleMainClick}/> :
          (currentPage >= 0  && currentPage <=6) ? <RatingPage goNext={this.handleNextRating} ratingQuestion={currentPage}/> :
          <StatPage data={this.state.ratingData}/>}
        </Grid>
        <Hidden only='xs'>
          <Grid sm={4}>
          </Grid>
        </Hidden>
      </Grid>
    )
  }
}

export default App;



