import React from 'react';

const style = {
    paddingTop:"10vh",
    minHeight: "10vh",
}

const buttonStyle = {
    backgroundColor: "rgb(12,204,185)",
    border: "none",
    borderRadius: "4px",
    color: "white",
    padding: "15px 32px",
    textDecoration: "none",
    display: "inline-block",
    fontSize: "150%",
}

const NextButton = ({onClick}) => {
    return(
        <div style={style}>
            <button style={buttonStyle} onClick={onClick}>
                Next
            </button>
        </div>
    )
}

export default NextButton;


